/**editer les informations d'une machine */
import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import {
    Card,
    CardBody,
    CardHeader,
    Row,
    Progress,
    Col,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    Form,
    NavItem,
    NavLink, } 
from 'reactstrap';
import classnames from 'classnames';
const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};

/*color for next and previous button */
const previousColor = "inverse"
const nextColor = "success"

const containerStyle = {
    width: '100%',
    height: '400px'
};


class Role extends Component {
    constructor(props) { 
        super(props);        
    }
    
    toggleStep = this.props.toggleStep
    validateOnChange = this.props.validateOnChange
    hasError = this.props.hasError
    handleSubmit = this.props.handleSubmit
    formRef = this.props.formRef
    swalCallback = this.props.swalCallback

    render() {     
        const nom = this.props.role.formRole.nom
        const description = this.props.role.formRole.description
        // tab item active
        const tab1 = this.props.role.progressValue[0]
        const tab2 = this.props.role.progressValue[1]
        const tab3 = this.props.role.progressValue[2]
        
        return (            
            <ContentWrapper>
                {/* START form */}
                <div className="block-center col-md-8">
                    <Form
                    innerRef={this.formRef}
                    className="form-horizontal" 
                    name="formRole" 
                    onSubmit={this.handleSubmit}
                    >
                        <Card className="card-default">
                            <CardHeader className="pb-0">
                                <Row>
                                    <div className="col pr-0"> <Progress color="success-light" value={tab1}/> </div>
                                    <div className="col p-0"> <Progress color="success-light" value={tab2}/> </div>
                                    <div className="col pl-0"> <Progress color="success-light" value={tab3}/> </div>
                                </Row>
                            </CardHeader>                            
                            <CardBody className="pt-0 pb-0">         
                                <Nav tabs justified >
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.role.activeStep === '1'
                                        })}
                                        onClick={this.toggleStep('1')}
                                        >
                                        <h4 className="text-left my-2">Informations</h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.role.activeStep === '2'
                                        })}
                                        onClick={this.toggleStep('2')}
                                        >
                                        <h4 className="text-left my-2">Fonctionnalités</h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.role.activeStep === '3'
                                        })}
                                        onClick={this.toggleStep('3')}
                                        >
                                        <h4 className="text-left my-2">Planning</h4>
                                        </NavLink>
                                    </NavItem>
                                </Nav>
                            </CardBody>    
                            <TabContent activeTab={this.props.role.activeStep}>
                                {/*nom, zone, adresse */}
                                <TabPane id="tabPane1" tabId="1">
                                    <div className="pt-3 mb-3">
                                        {/*nom */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nom</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="nom"
                                                name="nom" 
                                                placeholder="Nom" 
                                                invalid={this.hasError('formRole','nom','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={nom}
                                                />
                                                <span className="invalid-feedback">Veuillez entrer le nom du rôle</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*description */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Description</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="description"
                                                name="description" 
                                                placeholder="Description" 
                                                invalid={this.hasError('formRole','description', 'required')}
                                                onChange={this.validateOnChange}
                                                value={description}
                                                data-validate='["required"]' 
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                                                                            
                                            </div>
                                        </FormGroup>
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        {/*<Button color="secondary">Previous</Button>*/}
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        type="submit" 
                                        //onClick={this.toggleStep('2')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>   
                                </TabPane>
                                {/*fonctionnalités */}                        
                                <TabPane id="tabPane2" tabId="2">
                                    
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('1')}>
                                            Precedent
                                        </Button>
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        onClick={this.toggleStep('3')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>
                                </TabPane>
                                {/*planning */}
                                <TabPane id="tabPane3" tabId="3">      
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('2')}>
                                            Precedent
                                        </Button>
                                        <Button
                                            //type="submit" 
                                            className="ml-auto"
                                            color={nextColor}
                                        >                                              
                                            Terminer
                                        </Button>
                                    </div>
                                </TabPane>
                            </TabContent>                                            
                        </Card>
                        {/* END card */}
                    </Form>
                </div>
            </ContentWrapper>
            );
    }
 
}

export default Role;
